import React from "react"


export default function Home(props) {

  return (
    <div>
  
    

      <div style={{background:"#f4511e",color:"#ffffff"}} class="jumbotron text-center">
        <h1>    Premier Institute of Technology : PIS ,</h1>
        <p> Director :Ankush sir</p>
      </div>

      <div id="carouselExampleInterval" class="carousel slide" data-bs-ride="carousel">
        <div class="carousel-inner">
          <div class="carousel-item active" data-bs-interval="10000">
            <img  style ={{width: "300px" ,height: "600px"}}src="/icon.png" class="d-block w-100" alt="..." />
          </div>
          <div class="carousel-item" data-bs-interval="2000">
            <img  style ={{width: "300px" ,height: "600px"}}  src="/icon.png" class="d-block w-100" alt="..." />
          </div>
          <div class="carousel-item">
            <img  style ={{width: "300px" ,height: "600px"}} src="/icon.png" class="d-block w-100" alt="..." />
          </div>
        </div>
        <button class="carousel-control-prev" type="button" data-bs-target="#carouselExampleInterval" data-bs-slide="prev">
          <span class="carousel-control-prev-icon" aria-hidden="true"></span>
          <span class="visually-hidden">Previous</span>
        </button>
        <button class="carousel-control-next" type="button" data-bs-target="#carouselExampleInterval" data-bs-slide="next">
          <span class="carousel-control-next-icon" aria-hidden="true"></span>
          <span class="visually-hidden">Next</span>
        </button>
      </div>
    </div>
  );
}