import './App.css';
import React from 'react';
import { Route, Switch, HashRouter} from 'react-router-dom'  
import Navbar from './components/Navbar';
import Home from './components/Home';
import Form from './components/Form';
import LastyearResult from './components/Lastyr';
import OurPaststudent from './components/Paststu';
import CurrentResult from './components/Curr';

function App() {
  return (
    <>
      <HashRouter>
          <Navbar />

          <hr />

          <Switch>  
            <Route exact path="/Navbar"><Navbar/></Route>
            <Route exact path="/Lastyr"><LastyearResult/></Route>
            <Route exact path="/Home"><Home/></Route>
            <Route exact path="/Form"><Form/></Route>
            <Route exact path="/Paststu"><OurPaststudent/></Route>
            <Route exact path="/Curr"><CurrentResult/></Route>
            
          </Switch>
        </HashRouter>
 
    </>
  );
}

export default App;
